import React from 'react';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);   
  }
  
  handleClick() {
    console.log('button clicked');
  }
  
  render() { 
    return (
      <div className="wrapper">

        <div className="input">
          TEST
        </div>

        <div className="row">
          <button id="allClear" onClick={this.handleClick}>AC</button>
          <button id="%" onClick={this.handleClick}>%</button>
          <button id="+-" onClick={this.handleClick}>+-</button>
          <button id="delete" onClick={this.handleClick}>DEL</button>
        </div>
        <div className="row">
          <button id="7" onClick={this.handleClick}>7</button>
          <button id="8" onClick={this.handleClick}>8</button>
          <button id="9" onClick={this.handleClick}>9</button>
          <button id="/" onClick={this.handleClick}>รท</button>
        </div>
        <div className="row">
          <button id="4" onClick={this.handleClick}>4</button>
          <button id="5" onClick={this.handleClick}>5</button>
          <button id="6" onClick={this.handleClick}>6</button>
          <button id="*" onClick={this.handleClick}>x</button>
        </div>
        <div className="row">
          <button id="3" onClick={this.handleClick}>3</button>
          <button id="2" onClick={this.handleClick}>2</button>
          <button id="1" onClick={this.handleClick}>1</button>
          <button id="-" onClick={this.handleClick}>-</button>
        </div>
        <div className="row">
          <button id="." onClick={this.handleClick}>.</button>
          <button id="0" onClick={this.handleClick}>0</button>
          <button id="=" onClick={this.handleClick}>=</button>
          <button id="+" onClick={this.handleClick}>+</button>
        </div>
      </div>
    );
  }
}
 
export default App;